.App {
  text-align: center;
}

body {
  background-color: pink;
  padding: 0;
  margin: 0;
  box-sizing: border-box;

}


.App-link {
  color: #09d3ac;
}


button {
  margin: 0;
}


.status {
  margin-bottom: 10px;
}
.board-row {
  display: flex;
}
.square {
  background: #fff;
  border: 1px solid #999;
  // float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.game-info {
  margin-left: 20px;
}

